import { describe, expectTypeOf, it } from "vitest";
describe("Object definitions", () => {
    it("correctly upgrades attachment types at conversion time", () => {
        const attachment = {
            type: "attachment",
        };
        const attachmentArray = {
            type: "attachment",
            multiplicity: true,
        };
        expectTypeOf().toEqualTypeOf();
        expectTypeOf()
            .toEqualTypeOf();
    });
});
